@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;600;700&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Montserrat', sans-serif;
}
/* UKŁAD STRONY */
#root{ 
    display: flex;
    flex-wrap: wrap;
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
  }
  
  .navigation{
    position: fixed;
    left: 0;
    right: 0;
    height: 80px;
    box-shadow: 0 10px 30px 3px rgba(0, 0, 0, 0.128);
    background-color: white;
  }
  
  main{
    width: 100%;
    margin-top: 80px;
    background-color: #ccc;
    display: flex;
  }
  
  main .page{
    width: 85%;
    margin-left: 15%;
  }
  
  main aside{
    position: fixed;
    left: 0;
    width: 15%;
    height: calc(100% - 80px);
    display: flex;
    flex-direction: column;
    background-color: #333;
    color: white;
    padding: 100px 10px;
    text-align: center;
  }
  
  main aside .calendar{
    height: 50%;
  }
  main aside .weather{
    height: 50%;
  }

.navigation nav {
    display: flex;
    width: 80%;
    margin: 0 auto;
}

.navigation nav h1 {
    line-height: 60px;
    border-top: 10px solid transparent;
    width: 40%;
    font-weight: 700;
    color: rgb(192, 125, 1);
    font-style: italic;
    font-size: 30px;
}

.navigation nav h1 span {
    font-weight: 700;
    color: #333;
    font-style: italic;
    font-size: 36px;
    letter-spacing: 0;
}

.navigation nav ul li .active{
    font-weight: 600;
}

.navigation nav ul li:nth-of-type(1) .active {
    color: rgb(41,41,140);
    border-color: rgb(41, 41, 140);
}

.navigation nav ul li:nth-of-type(2) .active {
    color: rgb(32, 131, 32);
    border-color: rgb(32, 131, 32);
}
.navigation nav ul li:nth-of-type(3) .active {
    color: rgb(189, 172, 73);
    border-color: rgb(189, 172, 73);
}
.navigation nav ul li:nth-of-type(4) .active {
    color: rgb(128, 0, 109);
    border-color: rgb(128, 0, 109);
}

.navigation nav ul li:nth-of-type(1) a:hover{
    color: rgb(41,41,140);
}

.navigation nav ul li:nth-of-type(2) a:hover{
    color: rgb(32, 131, 32);
}

.navigation nav ul li:nth-of-type(3) a:hover{
    color: rgb(189, 172, 73);
}

.navigation nav ul li:nth-of-type(4) a:hover{
    color: rgb(128, 0, 109);
}


.navigation nav ul {
    width: 60%;
    display: flex;
    list-style: none;
}

.navigation nav ul li{
    width: 25%;
    text-decoration: none;
}
.navigation nav ul li a{
    display: block;
    height: calc(100% - 10px);
    width: 100%;
    font-size: 18px;
    color: #333;
    text-align: center;
    text-transform: uppercase;
    line-height: 70px;
    text-decoration: none;
    border-top: 10px solid transparent;
    cursor: default;
}


.calendar{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    flex-grow: 1;
    color: #ddd;
}

.calendar .time{
    width: 80%;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    font-size: 28px;
    font-weight: 600;
    padding-bottom: 15px;
}

.calendar .time div{
    width: 20%;
}

.calendar .time div:nth-of-type(2){
    width: 40%;
}

.calendar .dayNumber{
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 22px;
    font-weight: 600;
    padding-bottom: 20px;
}

.calendar .date{
    font-size: 20px;
}

.watchIcon{
    font-size: 60px;
    padding-bottom: 10px;
}
.page .homepage{
    background-color: rgb(41,41,140);
    color: white;
}
.preloader{
    -webkit-animation: rotate 2s infinite linear;
            animation: rotate 2s infinite linear;
}

@-webkit-keyframes rotate {
    0%{
        transform: rotate(0);
    }

    50%{
        transform: rotate(180deg);
    }

    100%{
        transform: rotate(360deg);
    }




}

@keyframes rotate {
    0%{
        transform: rotate(0);
    }

    50%{
        transform: rotate(180deg);
    }

    100%{
        transform: rotate(360deg);
    }




}
.article{
    height: 250px;
    width: 700px;
    margin: 20px auto;
    background-color: white;
    display: flex;
    border-radius: 10px;
}


.article .img{
   height: 100%;
   width: 35%;
   background-size: contain;
   border-radius: 10px 0 0 10px;
}

.article .content{
    width: 65%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px 20px;
}

.article .content .title{
    font-weight: 600;
    font-size: 18px;
    line-height: 1.5;
    color: #333;
}

.article .content button{
    display: block;
    align-self: flex-end;
    width: 50%;
    border: none;
}

.article .content button a{
    font-family: Montserrat,sans-serif;
    font-weight: 600;
    text-decoration: none;
    color: white;
    background-color: #666;
    border: none;
    padding: 20px 10px;
    text-transform: uppercase;
    display: block;
    align-self: flex-end;
    cursor: default;
    border-radius: 10px;
    transition: .2s ease-in;
}

.article .content button a:hover{
   background-color: #333;
}


.page .sport{
    background-color: rgb(32, 131, 32);
    color: white;
}
.page .bussines{
    background-color: rgb(189, 172, 73);
    color: #333;
}
.page .technology{
    color: white;
    background-color: rgb(128, 0, 109);
}


.page h2{
    position: fixed;
    left: 15%;
    right: 0;
    height: 120px;
    line-height: 120px;
    font-size: 40px;
    letter-spacing: 2px;
    text-align: center;
    box-shadow: 0 10px 30px 3px rgba(0, 0, 0, 0.128);
}

.page .container {
    width: 100%;
    height: 100%;
    margin-top: 120px;
    padding: 30px 20px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    background-color: #eee;
}

.page .container .article{
    width: 46%;
}


